import { ChangeDetectionStrategy, Component, OnInit, ViewChild } from '@angular/core';
import { NgbCarousel } from '@ng-bootstrap/ng-bootstrap';
import { Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';


interface ClubData {
    name: string;
    category: '' | 'football' | 'handball' | 'basketball' | 'ice-hockey' | 'hockey' | 'volleyball' | 'other';
    ligue: '' | number;
    spectators: null | number;
    homepage: string;
    facebook: string;
    twitter: string;
    instagram: string;
}

@Component({
    selector: 'sb-intro',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './intro.component.html',
    styleUrls: ['intro.component.scss'],
})
export class IntroComponent implements OnInit {
    @ViewChild('onboardingCarousel') imgCarousel: null | NgbCarousel;

    clubs: ClubData[] = [{
        name: 'Bonner SC',
        category: 'football',
        ligue: 4,
        spectators: null,
        homepage: 'https://www.bonner-sc.de/',
        facebook: 'https://www.facebook.com/BonnerSC',
        instagram: 'https://www.instagram.com/bonner__sc/',
        twitter: 'https://twitter.com/bonner_sc/',
    }];

    model: ClubData = {
        name: '',
        category: '',
        ligue: '',
        spectators: 0,
        homepage: '',
        facebook: '',
        instagram: '',
        twitter: '',
    };

    constructor() {
        this.imgCarousel = null;
    }

    updateName() {
        const clubCandidate = this.clubs.find(x => x.name === this.model.name);
        if (clubCandidate) {
            this.model = clubCandidate;
        }
    }

    search = (text$: Observable<string>) =>
        text$.pipe(
            debounceTime(0),
            distinctUntilChanged(),
            map(term => {
                return term.length < 1 ? []
                    : (this.clubs.map(c => c.name)).filter(v => v.toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10);
            }),
        );

    next() {
        this.imgCarousel?.next();

    }

    ngOnInit() {
    }
}
